.Planets__content{
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.Planets__container{
    border: 2px solid black;
    border-radius: 10px;
    background-color: white;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 500px;
    height: 250px;
    margin: 20px;
    align-items: left;
    text-align: left;
}

.Planets_input{
    display: flex;
    margin-left: 10px;
}
.Planets_label{
    display: flex;
    margin-left: 40px;
}