.Navbar_button{
    margin: 0 10px;
    background: transparent;
    border: 3px solid #ccc;
    border-radius: 20px;
    padding: 10px;
    color: #ccc;
    font-size: 1.2em;
    cursor: pointer;
    text-align: center;
    align-items: center;
}

.Navbar_button:hover{
    color: #fff;
    border-color: #fff;
}