.header {
    background-color: rgb(160, 4, 4);
    display: flex;
    align-items: center;
    position: sticky;
    top: 0;
    z-index: 100;
}

.header__logo{
    width: 50px;
    object-fit: contain;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
}

.header__optionBasket{
    display: flex;
    align-items: center;
}

.header__BasketCount{
    margin-left: 10px;
    margin-right: 10px;
}

.header__nav{
    display: flex;
    justify-content: space-evenly;
}

.header__option{
    display: flex;
    flex-direction: row;
    border-radius: 2px;
    width: 100%;
    height: 50px;
    border: 1px solid;
    border-color: darkgray;
    border-radius: 10px!important;
    align-items: center;
}

.header__optionLineOne{
    display: flex;
    margin-left: 10px;
    font-size: 20px;
    font-weight: 800;
}

.header__optionLineTwo{
    display: flex;
    font-size: 13px;
    font-weight: 800;
}

.header__link{
    color: white;
    margin-left: 10px;
    margin-right: 10px;
    
}


.header__search{
    display: flex;
    flex: 1;
    color: white;
}

.header__searchInput{
    height: 12px;
    padding: 10px;
    border: none;
    width: 0;
}

.header__searchIcon{
    padding: 5px;
    height: 22px !important;
    background-color: #d48722;
}