.Home__main{
    display: flex;
    flex-direction: column;
}
.Home__welcome{
    align-items: center;
    display: flex;
    flex-direction: row;
    margin-left: 10px;
    margin-right: 10px;
}

.Home__welcomeLogo{
    width: 50%;
    object-fit: contain;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 10px;
}

.Home__welcomePhrase{
    display: flex;
    flex-direction: column;
    align-items: center;
    margin-left: 150px;
    margin-right: 150px;
}

.Home__welcomePhrase>h1{
    text-align: center;
}

.Home__welcomePhrase>h3{
    text-align: justify;
}

.Home__welcomePhrase>h3{
    display: flex;
    flex-direction: column;
    align-items: center;
    
}

.Home__features{
    background-color: rgb(160, 4, 4);
    padding: 200px;
    align-items: center;
    display: flex;
    flex-direction: column;   
}

.Home__featuresPhrase{
    margin-bottom: 50px;
    color: white;
}

.Home__nav{
    display: flex;
    justify-content: space-evenly;
}

.Home__link{
    
    display: flex;
    flex-direction: column;
    color: white;
    margin-left: 10px;
    margin-right: 10px;
    flex-direction: column;
    text-decoration: none;
    padding: 10px;
    border-radius: 2px;
    border: 1px solid;
    border-color: white;
    border-radius: 10px!important;
    width: 200px;
    height: 200px;
}

.Home__option{
    margin-top: 60px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.Home__link:hover{
    color: black;
    border-color: black;
}

.Home__optionLineOne{
    
    font-size: 20px;
    font-weight: 800;
    text-align: center;
    
}

.Home__optionLineTwo{
    
    font-size: 13px;
    font-weight: 800;
}

.Home__Profile{
    padding: 200px;
    align-items: center;
    display: flex;
    flex-direction: column;   
}

.Home__profilePhrase{
    margin-bottom: 50px;
}



.Home__profileNav {
    display: flex;
    flex-direction: column;
    align-items: center;
    padding-top: 10px;
    padding-bottom: 10px;
    padding-left: 0px;
    padding-right: 0px;
    border-radius: 2px;
    border: 1px solid;
    border-color: black;
    border-radius: 10px!important;
    width: 300px;
    height: 300px;
}

.Home__ProfileLogo{
    width: 50%;
    object-fit: contain;
    margin-top: 10px;
    align-items: center;
}

.Home__profileoptionLineOne{
    font-size: 20px;
    font-weight: 600;
    margin-left: 2px;
    margin-right: 2px;
}

.Home__profileoptionLineTwo{
    font-size: 20px;
    font-weight: 600;
    margin-left: 2px;
    margin-right: 2px;
    text-align: justify;
}






