.Registerproduct {
  background: url("./background.jpg");
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  width: 100vw;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  height: 100vh;
}

.user {
  border: 2px solid black;
  border-radius: 10px;
  background-color: white;
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 500px;
  height: 300px;
  margin: 20px;
}

.user > h3 {
  margin-left: 30px;
}

.paginationBttns {
  height: 40px;
  list-style: none;
  display: flex;
  justify-content: center;
}

.paginationBttns a {
  padding: 10px;
  margin: 8px;
  border-radius: 5px;
  border: 1px solid #2b2eff;
  color: #2b2eff;
  cursor: pointer;
  background-color: white;
}

.paginationBttns a:hover {
  color: white;
  background-color: #2b2eff;
}

.paginationActive a {
  color: white;
  background-color: #2b2eff;
}
