.Budgetincome__content{
    display: flex;
    justify-content: center;
    align-items: left;
    text-align: left;
}

.Budgetincome__container{
    border: 2px solid black;
    border-radius: 10px;
    background-color: white;
    display: flex;
    justify-content: center;
    flex-direction: column;
    width: 500px;
    height: 300px;
    margin: 20px;
    align-items: left;
    text-align: left;
}

.Budgetincome_input{
    display: flex;
    margin-left: 10px;
}
.Budgetincome_label{
    display: flex;
    margin-left: 40px;
}