.Calculator1{
    background: url("./background.jpg");
    background-size: cover;
    background-position: center;
    background-repeat: no-repeat;
    height:100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

.Calculator1__addButton{
    border-radius: 2px;
    width: 50%;
    height: 30px;
    border: 1px solid;
    margin-top: 10px;
    border-color: darkgray;
    margin-left: 80px;
    border-radius: 20px!important;
}

.Calculator1__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 40px;
    border: 1px solid lightgray;
    border-radius: 20px!important;
    margin-left: 10px;
    margin-right: 10px;
}

.Variablecost__container{
    width: 300px;
    display: flex;
    flex-direction: column;
    padding: 40px;
    border: 1px solid lightgray;
    border-radius: 20px!important;
    margin-left: 10px;
    margin-right: 10px;
}

.Calculator1__containerRow{
    display: flex;
    justify-content: space-evenly;

}

.Calculator1__phrase2{
    margin-left: 150px;
    color: white;
}

.Calculator1__phrase3{
    color: white;
}

.Calculator1__phrase1{
    color: white;
    margin-left: 10px;
    margin-right: 10px;
  
}

.Calculator1__phrase4{   
    height: 30px;  
    margin-top: 10px;  
    margin-left: 20px;   
    color: white;
    
}

.Calculator1__row1{
    display: flex;
    height: 25px;
    padding: 20px;
    border: none;
    align-items: center;
    z-index: 1;  
}

.Calculator1__row2{
    display: flex;
    height: 25px;
    padding: 20px;
    border: none;  
    align-items: center;
    z-index: 1;
 
}


.Calculator1__row3{
    display: flex;
    height: 25px;
    padding: 20px;
    border: none;
    align-items: center;
    z-index: 1;
 
}
.Calculator1__phrase3{
    margin-left:25px;
}

.Calculator1__row1> input{
    padding: 10px;
}
.Calculator1__row2> input{
    padding: 10px;
}
.Calculator1__row3> input{
    padding: 10px;
}

.Calculator1__logo{
    width: 100px;
    object-fit: contain;
    margin-left: 20px;
    margin-right: 20px;
    margin-top: 18px;
}

.Calculator1__input{
    display: flex;
}